.chat {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: #121212;

  &__messages {
    flex: 1;
    overflow-y: auto;
    padding: 16px;
    background: #121212;

    &::-webkit-scrollbar {
      width: 8px;
    }

    &::-webkit-scrollbar-thumb {
      background: #1db954;
      border-radius: 4px;
    }

    &::-webkit-scrollbar-thumb:hover {
      background: #148f3d;
    }
  }

  &__form {
    display: flex;
    align-items: center;
    padding: 12px 20px;
    background: #181818;
    border-top: 1px solid #282828;
  }

  &__input {
    flex: 1;
    padding: 12px 16px;
    border: 1px solid #282828;
    border-radius: 24px;
    background: #121212;
    color: #ffffff;
    font-size: 14px;
    outline: none;
    transition: border-color 0.2s;

    &:focus {
      border-color: #1db954;
      background: #181818;
      box-shadow: 0 0 8px rgba(29, 185, 84, 0.6);
    }

    &::placeholder {
      color: #b3b3b3;
      font-size: 14px;
    }
  }

  &__button {
    margin-left: 12px;
    padding: 12px 20px;
    background: #1db954;
    color: #ffffff;
    border: none;
    border-radius: 24px;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.2s, transform 0.2s;

    &:hover {
      background: #148f3d;
      transform: translateY(-2px);
    }

    &:disabled {
      background: #3a3a3a;
      cursor: not-allowed;
    }
  }
}

@media (max-width: 768px) {
  .chat {
    height: 87vh;
  }

  .chat__form {
    &__input {
      margin-bottom: 12px;
      padding: 10px;
      font-size: 12px;
    }

    &__button {
      width: 100%;
      font-size: 12px;
    }
  }

  .chat__messages {
    padding: 12px;
  }
}

@media (max-width: 480px) {
  .chat__form {
    padding: 8px;

    &__input {
      font-size: 11px;
    }

    &__button {
      font-size: 11px;
    }
  }

  .chat__messages {
    padding: 8px;
  }
}
